.formstyle {
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 25px;
    margin-top: 25px;
    margin-bottom: 25px;
    background-color: burlywood;
    border: 2px solid black;
    border-radius: 15px;
}

@media screen and (min-width: 768px) {
    .formstyle {width: 400px;}   
}

.inputstyle {
    width: 100%;
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.inputname {
    font-size: large;
    font-weight: bold;
    color: #000000;
}

.button {
    appearance: none;
    background-color: #FFFFFF;
    border-radius: 40em;
    border-style: none;
    box-shadow: #ADCFFF 0 -12px 6px inset;
    box-sizing: border-box;
    color: #000000;
    cursor: pointer;
    display: inline-block;
    font-family: -apple-system, sans-serif;
    font-size: 1.2rem;
    font-weight: 700;
    letter-spacing: -.24px;
    margin-top: 15px;
    outline: none;
    padding: 1rem 1.3rem;
    quotes: auto;
    text-align: center;
    text-decoration: none;
    transition: all .15s;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
}

.button:hover {
    background-color: #FFC229;
    box-shadow: #FF6314 0 -6px 8px inset;
    transform: scale(1.125);
}

.button:active {
    transform: scale(1.025);
}

@media (min-width: 768px) {
    .button {
        font-size: 1.5rem;
        padding: .75rem 2rem;
    }
}